.about__container {
    display: grid;
    grid-template-columns: 15% 70%;
    gap: 15%;
}

.about__me {
    width: 150%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    height: min-content;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(8deg);
}

.about__me-image:hover {
    transform: rotate(0deg);
    transition: var(--transition);

}

.about__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
}

.about__card {

    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: left;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 2rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: .95rem;
}

.about__card h3 {
    font-size: .75rem;
    color: var(--color-primary);
}

.about__card a {
    color: var(--color-light);
}

.about__card hr {

    border: .3px solid;
    color: var(--color-primary);
}

.about__card small {
    font-size: 0.75rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

.project_list {
    display: grid;
    grid-template: 1fr;
    gap: 1rem;
}

.certification_list {
    display: grid;
    grid-template: 1fr;
    gap: 0.45rem;
    grid-auto-flow: dense;
}

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}


@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;

    }

    .about__cards {
        grid-template-columns: 1fr;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}